"use client";

import React, { useEffect, useState, useCallback } from 'react';
import { toast } from 'react-hot-toast';
import Button from '@/components/ui/Button';
import { Modal } from '@/components/ui/modal';
import Input from '@/components/form/input/InputField';
import Select from '@/components/ui/Select';
import { Gateway, GatewayType, ListGatewayParams } from '@/services/api/gateway.service';
import { gatewayService } from '@/services/api/gateway.service';
import GatewayCard from '@/components/bancos/GatewayCard';
import Pagination from '@/components/ui/Pagination';
import GatewayForm from '@/components/bancos/GatewayForm';
import Label from '@/components/form/Label';

type FilterOptions = {
  search?: string;
};

export default function BancosPage() {
  const [gateways, setGateways] = useState<Gateway[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedGateway, setSelectedGateway] = useState<Gateway | null>(null);
  const [filters, setFilters] = useState<FilterOptions>({});
  const [itemsPerPage, setItemsPerPage] = useState(9);

  const loadGateways = useCallback(async () => {
    try {
      setLoading(true);
      const params: ListGatewayParams = {
        page,
        limit: itemsPerPage,
        search: searchTerm
      };
      const response = await gatewayService.list(params);
      setGateways(response.data);
      setTotalPages(response.pagination.pages);
    } catch (error) {
      console.error('Erro ao carregar gateways:', error)
      toast.error('Erro ao carregar gateways');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, itemsPerPage]);

  useEffect(() => {
    loadGateways();
  }, [loadGateways]);

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
    setPage(1);
  };

  const handleFilterChange = (newFilters: Partial<FilterOptions>) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
    setPage(1);
  };

  const handleEdit = (gateway: Gateway) => {
    setSelectedGateway(gateway);
    setIsModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir este gateway?')) return;

    try {
      await gatewayService.delete(id);
      toast.success('Gateway excluído com sucesso');
      loadGateways();
    } catch (error) {
      toast.error('Erro ao excluir gateway');
    }
  };

  // Status toggle não está disponível na API

  const handleSave = async (data: any) => {
    try {
      if (selectedGateway) {
        await gatewayService.update(selectedGateway.id, data);
        toast.success('Gateway atualizado com sucesso');
      } else {
        await gatewayService.create(data);
        toast.success('Gateway criado com sucesso');
      }
      setIsModalOpen(false);
      setSelectedGateway(null);
      loadGateways();
    } catch (error) {
      toast.error('Erro ao salvar gateway');
    }
  };



  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-2">
              Bancos e Gateways
            </h1>
            <p className="text-sm text-gray-500">
              Gerencie as integrações de pagamento do sistema
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Button
              variant="primary"
              onClick={() => {
                setSelectedGateway(null);
                setIsModalOpen(true);
              }}
            >
              Novo Gateway
            </Button>
          </div>
        </div>

        <div className="mt-6 flex flex-col sm:flex-row items-center gap-4">
          <div className="flex-1 w-full sm:max-w-xs">
            <Input
              type="text"
              placeholder="Buscar gateways..."
              value={searchTerm}
              onChange={handleSearch}
              className="w-full"
            />
          </div>
        </div>
      </div>

      <div className="flex justify-between mb-4 gap-2 flex-wrap">
        <div className='flex items-center gap-0.5'>
          <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
          <Input
            className='max-w-24'
            defaultValue={itemsPerPage}
            max={100}
            min={1}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const newValue = Number(e.target.value) || 9;
              setItemsPerPage(newValue);
              setPage(1);
            }}
          />
        </div>
        <Pagination
          currentPage={page}
          totalPages={totalPages}
          onPageChange={setPage}
        />
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {gateways.length > 0 ? (
            gateways.map((gateway) => (
              <GatewayCard
                key={gateway.id}
                gateway={gateway}
                onEdit={() => handleEdit(gateway)}
                onDelete={() => handleDelete(gateway.id)}

              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhum gateway encontrado
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedGateway(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <GatewayForm
            gateway={selectedGateway}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedGateway(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}
