import React, { useState } from 'react';
import { toast } from 'react-hot-toast';
import { Gateway, GatewayType } from '@/services/api/gateway.service';
import Badge from '../ui/badge/Badge';
import ButtonCard from '../ui/button/ButtonCard';
import { api } from '@/services/api';

type GatewayCardProps = {
  gateway: Gateway;
  onEdit: () => void;
  onDelete: () => void;
};

// Mapeia código interno para label amigável e cor
const gatewayMeta: Record<GatewayType, { label: string; color: any }> = {
  MERCADO_PAGO: { label: 'Mercado Pago', color: 'primary' },
  OMIE: { label: 'Omie', color: 'warning' },
  ASAAS: { label: 'Asaas', color: 'success' },
  CORA: { label: 'Cora', color: 'info' },
  EFIPAY: { label: 'EFI Pay', color: 'warning' }
};


const gatewayFields = {
  MERCADO_PAGO: {
    access_token: {
      label: 'Access Token',
      type: 'password',
      required: true
    }
  },
  OMIE: {
    app_key: {
      label: 'App Key',
      type: 'text',
      required: true
    },
    app_secret: {
      label: 'App Secret',
      type: 'password',
      required: true
    },
    conta_corrente_id: {
      label: 'Conta Corrente ID',
      type: 'text',
      required: true
    },
  },
  ASAAS: {
    api_key: {
      label: 'API Key',
      type: 'password',
      required: true
    },
  },
  CORA: {
    client_id: {
      label: 'Client ID',
      type: 'text',
      required: true
    },
    cert_pem: {
      label: 'Certificado (.pem)',
      type: 'file',
      accept: '.pem',
      required: true
    },
    cert_key: {
      label: 'Private Key (.key)',
      type: 'file',
      accept: '.key',
      required: true
    },
  },
  EFIPAY: {
    client_id: {
      label: 'Client ID',
      type: 'text',
      required: true
    },
    client_secret: {
      label: 'Client Secret',
      type: 'password',
      required: true
    },
    chave_pix: {
      label: 'Chave PIX',
      type: 'text',
      required: true
    },
    cert_p12: {
      label: 'Certificado (.p12)',
      type: 'file',
      accept: '.p12',
      required: true
    },
  }
};

// Campos sensíveis que serão mascarados
const SENSITIVE_KEYS = ['token', 'secret', 'key', 'access_token', 'app_secret', 'api_key', 'client_secret', 'id', 'pem'];

const isSensitive = (key: string) => SENSITIVE_KEYS.some(k => key.toLowerCase().includes(k));

const formatKey = (key: string) =>
  key
    .replace(/_/g, ' ')
    .replace(/([a-z])([A-Z])/g, '$1 $2')
    .replace(/^./, c => c.toUpperCase());

const maskValue = (value: any) => {
  if (value === null || value === undefined) return '-';
  const str = String(value);
  if (str.length <= 8) return '*'.repeat(str.length);
  return str.slice(0, 4) + '••••' + str.slice(-4);
};

const GatewayCard: React.FC<GatewayCardProps> = ({ gateway, onEdit, onDelete }) => {
  const [expanded, setExpanded] = useState(false);
  const [revealedKeys, setRevealedKeys] = useState<Record<string, boolean>>({});
  const baseURL = api.defaults.baseURL || (typeof window !== 'undefined' ? window.location.origin + '/api/v1' : '');
  const url_webhook = baseURL ? `${baseURL}/webhook/gateways/${gateway.gateway.toLowerCase()}` : '';

  const meta = gatewayMeta[gateway.gateway as GatewayType];
  const fields = gatewayFields[gateway.gateway as GatewayType];
  const entries = Object.entries(gateway.dados || {}).filter(([key]) => fields[key as keyof typeof fields]);
  const visibleEntries = expanded ? entries : entries.slice(0, 4);


  const toggleReveal = (key: string) => {
    setRevealedKeys(prev => ({ ...prev, [key]: !prev[key] }));
  };

  const handleCopy = async (value: any) => {
    try {
      await navigator.clipboard.writeText(String(value));
      toast.success('Copiado para a área de transferência');
    } catch (err) {
      toast.error('Não foi possível copiar');
    }
  };

  return (
    <div className="bg-white dark:bg-gray-800 rounded-xl shadow-sm hover:shadow-md transition-shadow border border-gray-200 dark:border-gray-700 p-5 flex flex-col">
      <div className="flex justify-between gap-4 mb-4">
        <div className="min-w-0">
          <div className="flex items-center gap-2 flex-wrap">
            <h3 className="font-semibold text-gray-900 dark:text-white truncate max-w-[220px]" title={gateway.nome || meta?.label}>
              {gateway.nome || meta?.label}
            </h3>
            {meta && (
              <Badge color={meta.color} className="uppercase tracking-wide text-[10px] font-semibold">
                {meta.label}
              </Badge>
            )}
            {gateway.isDefault && (
              <Badge color="success" className="text-[10px] font-semibold">Padrão</Badge>
            )}
          </div>
          {gateway.descricao && (
            <p className="text-xs text-gray-500 dark:text-gray-400 mt-1 line-clamp-2" title={gateway.descricao}>
              {gateway.descricao}
            </p>
          )}
        </div>
        <div className="flex items-start gap-2 shrink-0">
          <ButtonCard
            variant="secondary"
            size="xs"
            onClick={onEdit}
            title="Editar"
            icon="edit"
          />
          <ButtonCard
            variant="secondary"
            size="xs"
            onClick={onDelete}
            title="Excluir"
            icon="delete"
          />
        </div>
      </div>

  <div className="space-y-3 flex-1">
        <h4 className="text-xs font-medium uppercase text-gray-500 dark:text-gray-400 tracking-wider">Configurações</h4>
        {entries.length === 0 && (
          <p className="text-sm text-gray-400">Nenhuma configuração definida.</p>
        )}
        <ul className="space-y-1">
          {visibleEntries.map(([key, value]) => {
            const sensitive = isSensitive(key);
            const revealed = revealedKeys[key];
            return (
              <li key={key} className="group border border-gray-100 dark:border-gray-700 rounded-lg px-2 py-1 bg-gray-50/60 dark:bg-gray-900/40 hover:bg-gray-100 dark:hover:bg-gray-900 transition-colors">
                <div className="flex items-start gap-3">
                  <div className="flex-1 min-w-0">
                    <p className="text-[11px] uppercase tracking-wide text-gray-500 dark:text-gray-500 font-medium mb-0.5">{formatKey(key)}</p>
                    <div className="text-sm font-mono break-all text-gray-800 dark:text-gray-200 select-text">
                      {sensitive && !revealed ? maskValue(value) : String(value)}
                    </div>
                  </div>
                  <div className="flex flex-col gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                    {sensitive && (
                      <button
                        type="button"
                        onClick={() => toggleReveal(key)}
                        className="text-[10px] px-2 py-1 rounded bg-gray-200 dark:bg-gray-700 hover:bg-gray-300 dark:hover:bg-gray-600 font-medium"
                        title={revealed ? 'Ocultar' : 'Revelar'}
                      >
                        {revealed ? 'Ocultar' : 'Ver'}
                      </button>
                    )}
                    <button
                      type="button"
                      onClick={() => handleCopy(value)}
                      className="text-[10px] px-2 py-1 rounded bg-gray-200 dark:bg-gray-700 hover:bg-gray-300 dark:hover:bg-gray-600 font-medium"
                      title="Copiar valor"
                    >
                      Copiar
                    </button>
                  </div>
                </div>
              </li>
            );
          })}
        </ul>
        {entries.length > 4 && (
          <button
            type="button"
            onClick={() => setExpanded(e => !e)}
            className="w-full mt-1 text-xs font-medium text-primary hover:underline"
          >
            {expanded ? 'Mostrar menos' : `Mostrar todas (${entries.length})`}
          </button>
        )}

        {/* Webhook URL Section */}
        {url_webhook && (
          <div className="mt-4 border border-gray-100 dark:border-gray-700 rounded-lg p-3 bg-gray-50/60 dark:bg-gray-900/40">
            <div className="flex items-center gap-2 mb-2">
              <h5 className="text-[11px] uppercase tracking-wide text-gray-500 dark:text-gray-400 font-semibold m-0">Webhook</h5>
              <span
                className="inline-flex items-center justify-center w-4 h-4 text-[10px] rounded-full bg-gray-200 dark:bg-gray-700 text-gray-600 dark:text-gray-300 cursor-help"
                title="Use esta URL no painel do gateway para receber notificações e atualizar automaticamente as faturas."
              >i</span>
            </div>
            <div className="flex items-start gap-2">
              <code className="text-[11px] font-mono break-all text-gray-700 dark:text-gray-200 flex-1 select-text">
                {url_webhook}
              </code>
              <button
                type="button"
                onClick={() => handleCopy(url_webhook)}
                className="shrink-0 text-[10px] px-2 py-1 rounded bg-gray-200 dark:bg-gray-700 hover:bg-gray-300 dark:hover:bg-gray-600 font-medium"
                title="Copiar URL"
              >
                Copiar
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default GatewayCard;
