import React, { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import { Gateway, CreateGatewayRequest, GatewayType } from '@/services/api/gateway.service';
import Input from '@/components/form/input/InputField';
import Select from '@/components/ui/Select';
import Button from '@/components/ui/Button';
import Label from '@/components/form/Label';
import { onFileChange } from '@/utils/file';

type FormErrors = {
  gateway?: string;
  nome?: string;
  dados?: Record<string, string>;
};

type GatewayFormProps = {
  gateway?: Gateway | null;
  onSubmit: (data: FormData) => Promise<void>;
  onCancel: () => void;
};

const gatewayOptions = [
  { value: 'MERCADO_PAGO', label: 'Mercado Pago' },
  // { value: 'OMIE', label: 'Omie' },
  { value: 'ASAAS', label: 'Asaas' },
  { value: 'CORA', label: 'Cora' },
  { value: 'EFIPAY', label: 'EFI Pay' }
];
interface FileField {
  label: string;
  type: "file";
  accept: string;
  required: true;
}
interface TextField {
  label: string;
  type: "text" | "password";
  required: boolean;
}

type Field = FileField | TextField;

const gatewayFields = {
  MERCADO_PAGO: {
    access_token: {
      label: 'Access Token',
      type: 'password',
      required: true
    }
  },
  OMIE: {
    app_key: {
      label: 'App Key',
      type: 'text',
      required: true
    },
    app_secret: {
      label: 'App Secret',
      type: 'password',
      required: true
    },
    conta_corrente_id: {
      label: 'Conta Corrente ID',
      type: 'text',
      required: true
    },
  },
  ASAAS: {
    api_key: {
      label: 'API Key',
      type: 'password',
      required: true
    },
    // ambiente: {
    //   label: 'Ambiente',
    //   type: 'select',
    //   options: [
    //     { value: 'sandbox', label: 'Sandbox' },
    //     { value: 'production', label: 'Produção' }
    //   ],
    //   required: true
    // }
  },
  CORA: {
    client_id: {
      label: 'Client ID',
      type: 'text',
      required: true
    },
    cert_pem: {
      label: 'Certificado (.pem)',
      type: 'file',
      accept: '.pem',
      required: true
    },
    cert_key: {
      label: 'Private Key (.key)',
      type: 'file',
      accept: '.key',
      required: true
    },
  },
  EFIPAY: {
    client_id: {
      label: 'Client ID',
      type: 'text',
      required: true
    },
    client_secret: {
      label: 'Client Secret',
      type: 'password',
      required: true
    },
    chave_pix: {
      label: 'Chave PIX',
      type: 'text',
      required: true
    },
    cert_p12: {
      label: 'Certificado (.p12)',
      type: 'file',
      accept: '.p12',
      required: true
    },
  }
};

const GatewayForm: React.FC<GatewayFormProps> = ({
  gateway,
  onSubmit,
  onCancel
}) => {
  const [uploadedFiles, setUploadedFiles] = useState<Record<string, File>>({});

  const [formData, setFormData] = useState<CreateGatewayRequest>({
    gateway: gateway?.gateway || 'MERCADO_PAGO',
    nome: gateway?.nome || '',
    descricao: gateway?.descricao || '',
    isDefault: gateway?.isDefault || false,
    dados: gateway?.dados || {},
    empresaId: gateway?.empresaId
  });
  // useEffect(() => {
  //   console.log('formdata:', formData)
  //   console.log('uploadedFiles:', uploadedFiles)
  // }, [formData])


  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const validateForm = () => {
    const newErrors: FormErrors = {};
    const selectedGatewayFields = gatewayFields[formData.gateway];

    if (!formData.nome) {
      newErrors.nome = 'Nome é obrigatório';
    }

    if (!formData.gateway) {
      newErrors.gateway = 'Gateway é obrigatório';
    }

    const dadosErrors: Record<string, string> = {};
    if (selectedGatewayFields) {
      Object.entries(selectedGatewayFields).forEach(([key, field]) => {
        if (field.required && !formData.dados[key]) {
          dadosErrors[key] = `${field.label} é obrigatório`;
        }
      });
    }

    if (Object.keys(dadosErrors).length > 0) {
      newErrors.dados = dadosErrors;
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // const handleSubmit = async (e: React.FormEvent) => {
  //   e.preventDefault();
  //   if (!validateForm()) return;

  //   setIsSubmitting(true);
  //   try {
  //     await onSubmit(formData);
  //   } catch (error) {
  //     toast.error('Erro ao salvar gateway');
  //   } finally {
  //     setIsSubmitting(false);
  //   }
  // };
  const handleSubmit = async (e: React.FormEvent) => {
  e.preventDefault();
  if (!validateForm()) return;

  setIsSubmitting(true);
  try {
    const data = new FormData();

    // adiciona campos normais
    data.append("gateway", formData.gateway);
    data.append("nome", formData.nome);
    data.append("descricao", formData.descricao || "");
    data.append("isDefault", String(formData.isDefault));
    data.append("empresaId", String(formData.empresaId || ""));

    // adiciona apenas nomes no objeto dados
    Object.entries(formData.dados).forEach(([key, value]) => {
      data.append(`dados[${key}]`, value);
    });

    // adiciona arquivos reais
    Object.entries(uploadedFiles).forEach(([key, file]) => {
      data.append(key, file);
    });
    console.log('FormData::', data);
    // return;

    await onSubmit(data); // seu onSubmit agora precisa aceitar FormData
  } catch (error) {
    toast.error('Erro ao salvar gateway');
  } finally {
    setIsSubmitting(false);
  }
};


  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
        <div>
          <Label htmlFor="gateway">Gateway</Label>
          <Select
            id="gateway"
            name="gateway"
            value={gatewayOptions.find(opt => opt.value === formData.gateway)}
            options={gatewayOptions}
            onChange={(option) => {
              if (option) {
                setFormData({
                  ...formData,
                  gateway: (option as { value: GatewayType }).value,
                  dados: {} // Limpa os dados ao trocar de gateway
                });
              }
            }}
            error={errors.gateway}
          />
        </div>

        <div>
          <Label htmlFor="nome">Nome</Label>
          <Input
            id="nome"
            name="nome"
            value={formData.nome}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => setFormData({
              ...formData,
              nome: e.target.value
            })}
            error={errors.nome}
          />
        </div>

        <div className="sm:col-span-2">
          <Label htmlFor="descricao">Descrição</Label>
          <textarea
          title="descricao"
            id="descricao"
            name="descricao"
            value={formData.descricao}
            onChange={(e) => setFormData({
              ...formData,
              descricao: e.target.value
            })}
          className="w-full rounded-lg border border-stroke bg-transparent px-4 py-3 outline-none focus:border-primary focus-visible:shadow-none dark:border-gray-600 dark:bg-gray-900 dark:focus:border-primary dark:text-white"
            rows={3}
          />
        </div>

        <div className="sm:col-span-2">
          <div className="flex items-center space-x-2">
            <input
              title="Gateway padrão"
              type="checkbox"
              id="isDefault"
              checked={formData.isDefault}
              onChange={(e) => setFormData({
                ...formData,
                isDefault: e.target.checked
              })}
              className="rounded border-gray-300 text-primary focus:ring-primary"
            />
            <Label htmlFor="isDefault">Gateway padrão</Label>
          </div>
        </div>

        {formData.gateway && gatewayFields[formData.gateway] && (
          <div className="sm:col-span-2">
            <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
              Configurações do Gateway
            </h3>
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
              {Object.entries(gatewayFields[formData.gateway]).map(([key, fields]) => {
                const field = fields;
                let accept = '.zip';
                if ("accept" in field) {
                  accept = field.accept;
                }
                return (
                  <div key={key}>
                    <Label htmlFor={key}>{field.label}</Label>
                    {field.type === 'select' ? (
                      <Select
                        id={key}
                        name={key}
                        value={(field as any).options?.find((opt: any) => opt.value === formData.dados[key])}
                        options={(field as any).options || []}
                        onChange={(option) => {
                          if (option) {
                            setFormData({
                              ...formData,
                              dados: {
                                ...formData.dados,
                                [key]: (option as any).value
                              }
                            });
                          }
                        }}
                        error={errors.dados?.[key]}
                      />
                    ) : field.type === 'file' ? (
                      <Input
                        id={key}
                        name={key}
                        type={field.type}
                        accept={accept} onChange={async (e: React.ChangeEvent<HTMLInputElement>) => {
                          const file = e.target.files?.[0];
                          if (!file) return;

                          setFormData({
                            ...formData,
                            dados: {
                              ...formData.dados,
                              [key]: file.name // apenas o nome vai no objeto dados
                            }
                          });

                          // opcional: guardar o File real em outro state
                          setUploadedFiles(prev => ({
                            ...prev,
                            [key]: file
                          }));
                        }}

                        error={errors.dados?.[key]}
                      />
                    ) : (
                      <Input
                        id={key}
                        name={key}
                        type={field.type}
                        value={formData.dados[key] || ''}
                        onChange={(e: React.ChangeEvent<HTMLInputElement>) => setFormData({
                          ...formData,
                          dados: {
                            ...formData.dados,
                            [key]: e.target.value
                          }
                        })}
                        error={errors.dados?.[key]}
                      />
                    )}
                  </div>
                )
              })}
            </div>
          </div>
        )}
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          variant="secondary"
          onClick={onCancel}
          disabled={isSubmitting}
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Salvando...' : gateway ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default GatewayForm;
